"use strict";

const domUtil = {};

// inputtextarea
domUtil.textSelect = (input, start, end) => {
	if( input.setSelectionRange ){
		input.setSelectionRange(start, end);
		input.focus();
	}else if( input.createTextRange ){
		let range = input.createTextRange();         
		range.moveStart("character", start);
		range.moveEnd("character",end);
		if( start === end ){
			range.collapse(true);
		};
		range.select();
	};
};

const DOC = document;

const isIE8 = domUtil.isIE8 = () => {
	return DOC.documentMode === 8;
};

// IEIE8
domUtil.isIEOld = (defaultVal, iEOldVal) => {
	if( isIE8() ){
		return iEOldVal;
	}else{
		return defaultVal;
	};
};

domUtil.clearDom = (node) => {
	let clearDiv = document.createElement('div');
	clearDiv.appendChild(node);
	clearDiv.innerHTML = '';
	clearDiv = null;
};

const getStaticFun = domUtil.getStaticFun = (className, method) => {
	if( className ){
		let fn = className[method];
		if( fn ){
			return fn;
		}else{
			return getStaticFun(className.__proto__, method);
		};
	};
};

domUtil.setStaticProps = (sub, par) => {
	let obj = limit.assign({}, par);
	// 
	['name', 'length', '__proto__', 'prototype'].forEach((val) => {
		delete obj[val];
	});
	limit.assign(sub, obj);
};

export default domUtil;




